/* Подключение шрифта в <head> HTML-файла */
@import url('https://fonts.googleapis.com/css2?family=Baskerville:wght@700&display=swap');

.video-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
}

.fullscreen-video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    transform: translate(-50%, -50%);
}

.coming-soon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;
    color: white;
    background-color: rgba(0, 0, 0, 0);
    padding: 2rem;
    border-radius: 10px;
    text-align: center;
    z-index: 1;
    opacity: 0;
    animation: fadeIn 2s forwards;
    font-family: 'Baskerville', serif;
    text-shadow: 
        1px 1px 2px rgba(255, 165, 0, 0.7), 
        -1px -1px 2px rgba(255, 165, 0, 0.7),
        1px -1px 2px rgba(255, 165, 0, 0.7),
        -1px 1px 2px rgba(255, 165, 0, 0.7); 
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.pre-screen {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.pre-screen img.background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
}

.play-button {
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.play-button img {
    width: auto; 
    height: auto;
    max-width: 350px; 
    max-height: 370px; 
    object-fit: contain; 
    transition: transform 0.3s ease, filter 0.3s ease;
}

.play-button:hover img {
    transform: scale(1.1); 
    filter: brightness(1.2); 
}
